<template>
  <v-container fluid>
    <v-card v-if="!overlay">
      <v-card-title class="text-h6 font-weight-bold">
        <v-icon left class="mr-2">mdi-cash-multiple</v-icon>
        {{title}}
      </v-card-title>
      <v-divider class="my-4"></v-divider>

      <v-row dense>
        <!-- Required -->
        <v-col cols="12" md="4">
          <v-card color="primary" dark class="pa-3" elevation="10">
            <v-row align="center">
              <v-col cols="3" class="d-flex justify-center">
                <v-icon large>mdi-cash</v-icon>
              </v-col>
              <v-col cols="9">
                <div class="text-subtitle-2">Required</div>
                <div class="text-h6 white--text">Tsh {{ summary.required.toLocaleString() }}</div>
              </v-col>
            </v-row>
          </v-card>
        </v-col>

        <!-- Paid -->
        <v-col cols="12" md="4">
          <v-card color="success" dark class="pa-3" elevation="10">
            <v-row align="center">
              <v-col cols="3" class="d-flex justify-center">
                <v-icon large>mdi-cash-check</v-icon>
              </v-col>
              <v-col cols="9">
                <div class="text-subtitle-2">Paid</div>
                <div class="text-h6 white--text">Tsh {{ summary.paid.toLocaleString() }}</div>
              </v-col>
            </v-row>
          </v-card>
        </v-col>

        <!-- Unpaid -->
        <v-col cols="12" md="4">
          <v-card color="red darken-1" dark class="pa-3" elevation="10">
            <v-row align="center">
              <v-col cols="3" class="d-flex justify-center">
                <v-icon large>mdi-cash-remove</v-icon>
              </v-col>
              <v-col cols="9">
                <div class="text-subtitle-2">Unpaid</div>
                <div class="text-h6 white--text">Tsh {{ summary.unpaid.toLocaleString() }}</div>
              </v-col>
            </v-row>
          </v-card>
        </v-col>
      </v-row>

      <!-- Progress -->
      <v-row class="mt-4">
        <v-col cols="12">
          <v-progress-linear
            :value="allPaymentPercentage"
            height="40"
            color="success"
            rounded
            striped
            class="mb-1"
          ></v-progress-linear>
          <div class="text-caption text-right">
            {{ allPaymentPercentage.toFixed(1) }}% Paid
          </div>
        </v-col>
      </v-row>
      <StreamsFeesSummary :streamsSummary="summary.streams"></StreamsFeesSummary>
    </v-card>
  </v-container>
</template>
<script>
  export default {
    name: 'PaymentsSummary',
    props: {
      summary: {
        type: Object,
        required: true
      },
      title: {
        type: String,
        default: 'Payment Summary'
      },
      overlay: {
        type: Boolean,
        default: false
      }
    },
    computed: {
      allPaymentPercentage() {
        return this.summary.paid / this.summary.required * 100;
      }
    },
    components: {
      "StreamsFeesSummary": () => import("./StreamsFeesSummary.vue"),
    }
  }
</script>